#popup {
    width : 680px;
/*    min-height: 480px;*/
    padding: 1em 0;
    border: solid 1px #ddd;
}

.page{
  margin :0 1em;
  padding: 1em;
  border-radius: 4px;
  margin-bottom: 0;
}

.course-page .course{
  margin-bottom:1em;
}

.course-page .course-section{
  padding:.5em 0;
}

.course-page .accordion-button:focus {
    z-index: 3;
    border-color: transparent;
    outline: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
}

.course-page .accordion-button:not(.collapsed) {
    color: inherit; 
    background-color: transparent;
    -webkit-box-shadow: none;
            box-shadow: none; 
}

.course-page .accordion-button:not(.collapsed),
.course-page .accordion-button.collapsed,
.course-page .accordion-button:not(.collapsed)::after,
.course-page .accordion-button.collapsed::after{
  background:none;
}

.course-page .accordion-button.custom {
position: absolute;
    width: 24px;
    padding: 0px 6px;
    left: 42px;
    margin-top: 3px;
    background: none;
    font-size: 10px;
 }

.course-page .accordion-body{
  padding:0;
 }

.welcome-page .action-cnt{
  text-align:center;
  padding:.5em;
}

.welcome-page .btn-cnt{
  margin:1em;
}

.welcome-page{
  color: red;
}

#popup > .app-container > .console{
 margin-bottom: -33px;
  width: 100%;
}

.page-navigation ul.btn-group{
  margin: 0;
  padding: 0;
}